.bubble-menu {
  display: block;
  margin: 0 auto 0;
  width: 50px;
  height: 50px;
  padding: 0;
  position: fixed;
  right: 20px;
  top: 20px;
  z-index: 2; }

.bubble-menu:after {
  content: " ";
  z-index: 6;
  position: absolute;
  background-image: url('../images/icon-sandwich.svg');
  background-size: 50%;
  width: 50px;
  height: 50px;
  left: 0;
  top: 0;
  opacity: 1;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0);
  transition: opacity 0.2s ease-in, background-color 1s ease-in; }

.bubble-menu a {
  background-color: inherit; }

.bubble-menu li:hover .bubble-menu-label {
  display: block; }

.bubble-menu-label {
  font-size: 11px;
  color: #FFF;
  background-color: inherit;
  position: absolute;
  right: 30px;
  padding: 5px 10px;
  top: 10%;
  height: auto;
  display: block;
  line-height: 1em;
  font-family: 'Avenir';
  display: none; }

.bubble-menu li {
  display: block;
  width: 50px;
  height: 50px;
  background-color: blue;
  border-radius: 50%;
  opacity: 0.5;
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.12s ease-out, opacity 0.12s ease-out, color 0.12s ease-out;
  text-align: center;
  line-height: 50px;
  font-family: 'fontawesome';
  color: transparent; }
  .bubble-menu li .icon, .bubble-menu li .bubble-menu:after {
    transition: opacity 0.2s ease-out;
    opacity: 0; }

.bubble-menu li:first-child:after {
  display: none; }

.bubble-menu li:nth-child(1) {
  background-color: transparent;
  transform: translate(0, 0);
  z-index: 4;
  transition-duration: 0.12s; }

.bubble-menu li:nth-child(2) {
  background-color: #004DB3;
  transform: translate(0, -10%);
  z-index: 3;
  transition-duration: 0.24s; }

.bubble-menu li:nth-child(3) {
  background-color: #26B85A;
  transform: translate(10%, 0);
  z-index: 2;
  transition-duration: 0.36s; }

.bubble-menu li:nth-child(4) {
  background-color: #FFCB00;
  transform: translate(0, 10%);
  z-index: 1;
  transition-duration: 0.48s; }

.bubble-menu li:nth-child(5) {
  background-color: #D88E8E;
  transform: translate(-10%, 0);
  z-index: 0;
  transition-duration: 0.6s; }

.bubble-menu:hover, .bubble-menu.active {
  height: 220px; }
  .bubble-menu:hover:after, .bubble-menu.active:after {
    background-color: rgba(0, 0, 0, 0.5); }
  .bubble-menu:hover li, .bubble-menu.active li {
    color: white; }
  .bubble-menu:hover .icon, .bubble-menu:hover .bubble-menu:after, .bubble-menu.active .icon, .bubble-menu.active .bubble-menu:after {
    opacity: 1; }
  .bubble-menu:hover li:nth-child(1), .bubble-menu.active li:nth-child(1) {
    transform: translate(0, 110%);
